import React from "react";
import { Card } from "react-bootstrap";
import "../App.css";

const AddEvent = () => {
  const addEvents = [
    {
      id: 46,
      date: " Sunday, July 27 at 5 PM  ",

      text: "SummerFest: Alborada's Annual Gala: Lola Latin Bistro, Metuchen ",
      link: "https://lolalatinbistro.com/",
    },
    {
      id: 47,
      date: "Friday, 7 PM, August 15:",
      text: "River Dance Festival: Roxey Ballet, New Hope, PA ",
      link: "https://www.roxeyballet.org/riverdancefestival",
    },
    {
      id: 48,
      date: "October 4 (7:30 PM) ",
      text: "Alborada Spanish Dance Theatre 30th Anniversary Concert: Playhouse 22, East Brunswick, NJ ",
      link: "https://www.alboradadance.org/",
    },
    {
      id: 49,
      date: "Saturday, December 6",
      text: "Cantabile Chorale: Spanish Choral Music and Flamenco Dance, Piscataway, NJ",
      link: "https://cantabilechamberchorale.com/",
    },
  ];
  return addEvents ? (
    addEvents.map((event) => (
      <>
        <Card.Text key={event.id} className="text-warning">
          <p className="larger">
            <span style={{ color: "yellow", fontWeight: "bold" }}>
              {" "}
              {event.date}:{" "}
            </span>
            <Card.Link
              className="cardlink "
              href={event.link}
              target="blank"
              style={{ color: "white" }}
            >
              {event.text} <br></br>
            </Card.Link>

            {event.nonlink}
          </p>{" "}
        </Card.Text>
      </>
    ))
  ) : (
    <Card.Text>No upcoming at this time</Card.Text>
  );
};

export default AddEvent;
